.divider {
  display: block;
}
@media (min-width: 680px) {
  .divider {
    display: inline;
  }
  .divider:after {
    content: ' | ';
  }
}
/* ==================== General ==================== */
.ru-btn {
  background: #004c9b;
  border: 2px solid #004c9b;
  border-radius: 5px;
  box-shadow: 0px 5px #002d72;
  color: #ffffff;
  font-size: 1.25em;
  margin: 10px 5px 10px 5px;
  padding: 10px 20px 10px 20px;
  text-transform: uppercase;
}
.ru-btn:hover {
  box-shadow: 0px 3px #002d72;
  transform: translate(0, 2px);
}
.ru-btn:active {
  box-shadow: 0px 0px #002d72;
  transform: translate(0, 5px);
}
.ru-form-group-vertical {
  display: block;
}
.ru-form-group-vertical .ru-control-label {
  display: block;
  font-size: 1em;
  padding: 5px;
  margin: 5px 0 5px 0;
}
.ru-form-group-vertical .ru-form-control {
  text-align: left;
  border: 1px solid #999999;
  border-radius: 5px;
  display: block;
  font-size: 1em;
  padding: 5px 10px 5px 10px;
}
.ru-form-group-horizontal {
  display: block;
}
.ru-form-group-horizontal .ru-control-label {
  display: inline;
  font-size: 1em;
  padding: 5px;
  margin: 5px 0 5px 0;
}
.ru-form-group-horizontal .ru-form-control {
  text-align: left;
  border: 1px solid #999999;
  border-radius: 5px;
  display: inline;
  font-size: 1em;
  padding: 5px 10px 5px 10px;
}
